<template>
  <base-form>
    <form @submit.prevent="requestPasswordReset"> 
      <h1>Zmie haso</h1>
      <div class="input">
        <base-input type="email" v-model.trim="email" :valid="isEmailValid"></base-input>
        <p v-if="!isEmailValid">Podaj poprawny adres email.</p>
      </div>
      <base-button v-if="!isSending" type="green-large">Wylij</base-button>
      <BaseLoadingSpinner v-if="isSending"></BaseLoadingSpinner>
      <div>
        <p>Jeli nie posiadasz jeszcze konta moesz przej do <RouterLink :to="{name: 'signup'}">formularza rejestracji</RouterLink>.</p>
        <p>Jeli chcesz si zalogowa moesz przej do <RouterLink :to="{name: 'login'}">formularza logowania</RouterLink>.</p>
      </div>
    </form>
  </base-form>
</template>

<script>
  import BaseForm from '@/components/base/BaseForm.vue';
  import BaseButton from '@/components/base/BaseButton.vue';
  import BaseInput from '@/components/base/BaseInput.vue';
  import BaseLoadingSpinner from '@/components/base/BaseLoadingSpinner.vue';
  import inputValidators from '@/mixins/inputValidators';
  import { mapActions, mapGetters } from 'vuex';
  import { RouterLink } from 'vue-router';
  
  export default {
    props: ['id'],
    mixins: [inputValidators],
    components: {
    BaseForm,
    BaseButton,
    BaseInput,
    BaseLoadingSpinner,
    RouterLink
    },
    data() {
      return {
        email: '',
        isEmailValid: true,
        isSending: false
      }
    },
    computed: {
      ...mapGetters(['getNotificationTemplates']),
      isRequestOnly() {
        if (this.id==null)
          return true;
        return false;
      }
    },
    methods: {
      ...mapActions(['showNotification']),
      isFormValid(){
        this.isEmailValid = this.validateEmail(this.email);
        return this.isEmailValid;
      },
      requestPasswordReset() {
        if(this.isFormValid()) {
          this.showNotification(this.getNotificationTemplates.resetpassword_email_sent);
          console.log()
        }
        else {
          console.log()
        }
      }
    }
  }
</script>
  
<style scoped>
  .input {
    display: flex;
    flex-direction: column;
    gap:5px;
    align-items: center;
  }

  form {
    display: flex;
    gap: 20px;
    flex-direction: column;
    align-items: center;
  }
  
  p {
    font-size: medium;
    margin: 0;
    text-align: justify;
    text-justify: inter-word;
  }

  a {
    color: white;
  }
</style>