export default {
  state() {
    return {
      notifications: [],
      templates: {
        account_created: {
          label:'Konto zostao utworzone pomylnie',
          description:'Link aktywacyjny zosta wysany na twoj skrytk pocztow.',
          type: 'info'
        },
        resetpassword_email_sent: {
          label:'Email zosta pomylnie wysany',
          description:'Link do zmiany hasa zosta poprawnie wygenerowany i wysany na twoj skrytk pocztow. ',
          type: 'info'
        },
        password_changed: {
          label:'Haso zostao pomylnie zmienione',
          description:'Podczas logowania skorzystaj z nowego hasa.',
          type: 'info'
        },
        registration_form_invalid: {
          label:'Wypenij formularz poprawnymi danymi.',
          description:'Pamitaj, e haso musi posiada minimum 8 znakw oraz powinno zawiera ma i du liter, cyfr oraz znaku specjalny.',
          type: 'error'
        },
        common_error: {
          label:'Co poszo nie tak!',
          description:'Wystpi nieznany bd.',
          type: 'error'
        },
        login_form_invalid: {
          label:'Wypenij formularz poprawnymi danymi.',
          description:'Podaj prawidowy email oraz haso.',
          type: 'error'
        },
        user_logged: {
          label: 'Zalogowae sie pomylnie!',
          description: 'Zsoatniesz przekierowany do panelu uytkownika.',
          type: 'info'
        }
      }
    }
  },
  mutations: {
    addNotification(state, notification) {
      state.notifications.push(notification);
    },
    removeNotification(state, id) {
      state.notifications = state.notifications.filter(el => el.id !=id )
    }
  },
  actions: {
    showNotification(context, notification) {
      const n = {
        id: new Date().getTime(),
        ...notification
      };
      context.commit('addNotification', n);

      setTimeout(() => {
        context.dispatch('hideNotification', n.id);
      }, 60000);
    },
    hideNotification(context, id) {
      context.commit('removeNotification', id);
    }
  },
  getters: {
    getNotificationTemplates(state) {
      return state.templates;
    },
    getNotifications(state) {
      return state.notifications;
    }
  }
};